<template>
  <page-content>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="24">
          <a-col :md="12" :sm="12">
            <a-form-item label="开始时间">
              <a-date-picker v-model="queryParams.startTime" show-time placeholder="选择开始时间" @change="onStartChange" @ok="onStartOk" />
            </a-form-item>
            <a-form-item label="截止时间">
              <a-date-picker v-model="queryParams.endTime" show-time placeholder="选择截止时间" @change="onEndChange" @ok="onEndOk" />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-form-item label="关键字">
              <a-input v-model="queryParams.keywords" placeholder="输入关键字" style="width: 250px" />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-button type="primary" @click="search">查询</a-button>
            <a-button style="margin-left: 8px" @click="resetSearchForm()">重置</a-button>
          </a-col>
        </a-row>
      </a-form>
    </div>
    <div class="table-operator">
      <div class="batch-control pull-left">
      </div>
    </div>
    <a-table
      :columns="columns"
      :data-source="playRecords"
      :pagination="false"
      :rowSelection="rowSelection"
      ref="table"
      :loading="loading"
      rowKey="id"
    >
      <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
      <span slot="date" slot-scope="date">{{ date | filterDate(date) }}</span>
    </a-table>
    <lz-pagination :page="paginator" @change="changePaginator" @showSizeChange="showSizeChange" :autoPush="false" />
  </page-content>
</template>

<script>
import {
    getPlayRecords
} from '@/api/resource'
import Space from '@c/Space'
import LzPagination from '@c/LzPagination'
import { getRouteByCode } from '@/libs/utils'
import PageContent from '@c/PageContent'
import moment from 'moment'
import PermissionButton from '@/components/PermissionButton.vue'
const defaultQueryParams = {
    keywords: '',
    startTime: '',
    endTime: '',
    offset: 1,
    limit: 10
}
export default {
    name: 'Index',
    scroll: true,
    components: {
        LzPagination,
        Space,
        PermissionButton,
        PageContent
    },
    data () {
        return {
            visible: false,
            confirmLoading: false,
            loading: false,
            queryParams: defaultQueryParams,
            selectedRowKeys: [],
            selectedRows: [],
            columns: [
                {
                    title: 'ID',
                    dataIndex: 'id',
                    key: 'id'
                },
                {
                    title: '设备唯一编码',
                    dataIndex: 'code',
                    key: 'code'
                },
                {
                    title: '流媒体服务id',
                    dataIndex: 'media_server_id',
                    key: 'media_server_id'
                },
                {
                    title: '客户端ip',
                    dataIndex: 'client_ip',
                    key: 'client_ip',
                    ellipsis: true
                },
                {
                    title: 'rtp端口号',
                    dataIndex: 'server_port',
                    key: 'server_port'
                },
                {
                    title: '开播时间',
                    dataIndex: 'startTime',
                    key: 'startTime',
                    ellipsis: true,
                    scopedSlots: { customRender: 'time' }
                }
            ],
            playRecords: [],
            paginator: null,
            hasEdit: false,
            hasDelete: false
        }
    },
    mounted () {
        this.loadData()
        // this.checkPermissions()
    },
    computed: {
        rowSelection () {
            return {
                selectedRowKeys: this.selectedRowKeys,
                onChange: this.onSelectChange
            }
        }
    },
    methods: {
        onStartChange (value, dateString) {
          console.log('Selected Time: ', value)
          console.log('Formatted Selected Time: ', dateString)
        },
        onStartOk (value) {
          console.log('onOk: ', value)
        },
        onEndChange (value, dateString) {
          console.log('Selected Time: ', value)
          console.log('Formatted Selected Time: ', dateString)
        },
        onEndOk (value) {
          console.log('onOk: ', value)
        },
        handleCancel () {
            this.visible = false
        },
        resetSearchForm () {
            this.queryParams = defaultQueryParams
        },
        search () {
          this.loadData()
          this.resetPagination()
        },
        refresh (resetSearch = true) {
            this.loadData()
            if (resetSearch) {
                this.resetSearchForm()
            }
        },
        resetPagination () {
            this.queryParams.offset = 1
            this.queryParams.limit = 10
        },
        showSizeChange (current, perPage) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: current,
                limit: perPage
            })
            this.refresh()
        },
        changePaginator (page, pageSize) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: page,
                limit: pageSize
            })
            this.refresh()
        },
        async loadData () {
            this.loading = true
            console.log('this.queryParams:', this.queryParams)
            const { records, paginator } = await getPlayRecords(this.queryParams)
            this.playRecords = records
            this.paginator = paginator
            this.loading = false
        },
        dateFormat (row, column) {
            var date = row[column.property]
            console.log('date:', date)
            if (!date) {
                return ''
            }
            return moment(date).format('YYYY-MM-DD HH:mm:ss')
        },
        onSelectChange (selectedRowKeys, selectedRows) {
            this.selectedRowKeys = selectedRowKeys
            this.selectedRows = selectedRows
        }
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        }
    }
}
</script>
<style lang="less">
.del-span {
    text-decoration: line-through;
    color: red;
}
.pwd-border-none {
    .ant-input {
        border: none;
        padding-left: 0px;
        &:focus {
            border: none;
        }
    }
}
</style>
